import React from 'react';
import Resume from "../Resume/Sukumar-V-Resume.pdf"
import {Link} from "react-scroll"
import {Box,  Button,  Container,  Flex,  SimpleGrid, Spacer, Text} from "@chakra-ui/react"
const Navbar = () => {
function OpenTab() {
  window.open(
    `https://drive.google.com/file/d/1Dp1lk10apqwgii1N0iT0LyFziDyHtArP/view?usp=sharing`
    );
}
  return (
    <div id="nav-menu">
      <Box  left={'2.5rem'} right={'2.5rem'} top={0} mr={'-2.5em'} ml={"-2.5rem"} fontSize={25} fontWeight={400} fontFamily={'Ubuntu, sans-serif'} position={'fixed'} justifyContent={'space-between'} bg={'#A5D6A7'}   >
        <Flex justifyContent={"space-between"} w={"90%"} m={"auto"} >
          <Box>
            <Text as='h3'> <i> &lt; SUKUMAR /&gt;</i></Text>
          </Box>
          {/* <Spacer/> */}
          <Box display={"flex"} flexDirection={"row"}  gap={10} >
              <Box ml={25}>
               <Link to='home'spy={true} smooth={true} activeClass='="activeClass' offset={-100} duration={600}><Text color={'grey'} _hover={{color:'black' , cursor:'pointer'}} class="nav-link home"   ><i>Home</i> </Text></Link> 
              </Box>
              <Box ml={15}>
               <Link to='about'spy={true} smooth={true} activeClass='="activeClass' offset={-100} duration={600}><Text class="nav-link about" color={'grey'} _hover={{color:'black', cursor:'pointer'}} ><i>About</i></Text></Link>
              </Box>
              <Box ml={25}>
               <Link to='skills'spy={true} smooth={true} activeClass='="activeClass' offset={-100} duration={600}><Text  class="nav-link skills" color={'grey'} _hover={{color:'black', cursor:'pointer'}} ><i>Skills</i> </Text></Link>
              </Box>
              <Box ml={25}>
                <Link to='projects'spy={true} smooth={true} activeClass='="activeClass' offset={-100} duration={600}><Text class="nav-link projects" color={'grey'} _hover={{color:'black', cursor:'pointer'}} ><i>Projects</i> </Text></Link>
              </Box>
              <Box ml={25}>
                <a id="resume-link-1" href={Resume} download className="nav-link resume" style={{textDecoration:'none'}}><Text  id="resume-button-1" color="black" _hover={{color:'#43A047'}} onClick={OpenTab}><b><i>Resume</i> </b> </Text></a>
              </Box>
              <Box mt={20} ml={25}>
              <Link to='contact'spy={true} smooth={true} activeClass='="activeClass' offset={-100} duration={600}><Button className="nav-link contact"  cursor={'pointer'} pt={5} pb={5} pr={10}  pl={10}  border={'1px solid #4CBB17'} bg={'#4CBB17'} _hover={{bg:'white',color:'black'}} color={'white'} borderRadius={'20px'} fontSize={25} boxShadow={'0 20px 24px 3px #A5D6A7;'}><i>Contact</i> </Button></Link>
              </Box>  
          </Box>       
        </Flex>       
      </Box>            
    </div>
  )
}

export default Navbar